/* contact form*/


* {
  box-sizing: border-box;

}

.background_contact{
  
}

.Contact{

  max-width: 420px;
  margin: 0 auto;
  padding-top: 10%;
  padding-bottom: 10%;
  background-color: white;
}

p {
  text-align: center;
  font-weight: 500;
  font-size: 2rem;
}
input[type=text],input[type=email], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}
input[type=submit] {
  background-color: #4CAF50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
input[type=submit]:hover {
  background-color: #45a049;
}





  

